.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  height: 50px;
}
.date {
  font-family: 'Alegreya Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  color: #332821;
}
.range {
  display: flex;
  flex-direction: row;
  width: 60px;
  justify-content: space-between;
}
.icon {
  width: 50px;
  height: 50px;
}